@import url(https://fonts.googleapis.com/css2?family=Signika&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}



.weather {
    height: auto;
    width: 800px;
    background-image: linear-gradient(to top, #30cfd0 0%, #330867 100%);
    border-radius: 20px;
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 50px 0;
    font-family: 'Signika', sans-serif;
}

.weatherRow {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    padding: 10px;
}

.weatherColumn {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding: 10px;
}

button {
    height: 30px;
    width: 80px;
    margin-left: 10px;
    font-size: 15px;
    /*color: steelblue;*/
    border-radius: 2px;
    /*background-image: linear-gradient(to top, #d9afd9 0%, #97d9e1 100%);*/
    background-color: gold;
    font-family: 'Signika', sans-serif;
}

button:active {
    -webkit-transform: translateY(1px);
            transform: translateY(1px);
}

.searchBar {
    margin-top: 30px;
    -webkit-justify-content: center;
            justify-content: center;
}

input {
    width: 60%;
    height: 30px;
    border-radius: 2px;
    padding-left: 1%;
    font-family: 'IBM Plex Mono', monospace;
}

input[type="search"].my-search {
    -webkit-appearance: searchfield;
}

input[type="search"].my-search::-webkit-search-cancel-button {
    -webkit-appearance: searchfield-cancel-button;
    padding-right: 1%;
}

::-webkit-input-placeholder {
    color: rgba(51, 51, 48, 0.5);
    font-size: 12px;
    font-family: 'IBM Plex Mono', monospace;
}

:-ms-input-placeholder {
    color: rgba(51, 51, 48, 0.5);
    font-size: 12px;
    font-family: 'IBM Plex Mono', monospace;
}

::placeholder {
    color: rgba(51, 51, 48, 0.5);
    font-size: 12px;
    font-family: 'IBM Plex Mono', monospace;
}

.currLocation {
    font-size: 30px;
    margin: 15px 0;
}

.degree {
    font-size: 70px;
}

.currInfo {
    font-size: 16px;
    text-align: left;
}

.hourly {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-flow: row nowrap;
            flex-flow: row nowrap;
    -webkit-align-items: center;
            align-items: center;
    width: 80%;
    height: 45px;
    border-radius: 1px;
    border:1px solid #4C6095;
    margin-bottom: 5px;
    font-size: 12px;
}

.hourlyBoard {
    margin-bottom: 30px;
    -webkit-align-items: center;
            align-items: center;
}

.dailyRow {
    font-size: 12px;
    padding-top: 10%;
}

.daily {
    padding: 10px;
    cursor: pointer;
    border-radius: 2px;
    width: 11%;
}

.dailyBoard {
    padding: 10px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}

.switchBoard {
    font-size: 14px;
    text-decoration: underline;
    color: orange;
    cursor: pointer;
}

@media screen and (max-width: 800px) {
    .weather, .weatherRow, .weatherColumn, .searchBar {
        -webkit-flex-direction: column;
                flex-direction: column;
    }

    .weather {
        width: 100%;
        border-radius: 0;
        margin: 0;
    }

    .searchBar {
        margin-top: 10%;
    }

    .degree {
        margin: 10% 0;
    }

    button {
        margin-top: 10px;
    }

    .currInfo {
        margin-top: 5px;
        text-align: center;
    }

    .switchBoard {
        margin-top: 5%;
    }

    .hourlyBoard {
        margin-bottom: 10%;
    }

    .daily {
        -webkit-flex: 1 0 50%;
                flex: 1 0 50%;
    }

    .dailyBoard {
        margin: 0 15%;
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        -webkit-flex-direction: initial;
                flex-direction: initial;
    }
}




